/**
 * Select and return an element
 */
function Core(selector, container) {
	return typeof selector === "string"? (container || document).querySelector(selector) : selector || null;
}

/**
 * Select and return an array of elements
 */
Core.$$ = function(selector, container) {
	return typeof selector === "string"? (container || document).querySelectorAll(selector) : selector || null;
};

/**
 * DOM ready callback function
 */
Core.ready = function(fn) {
	document.addEventListener("DOMContentLoaded", fn);
};

/**
 * Scroll to an element
 */
Core.scrollTo = function(el, options) {
	const offset = !options || options.offset === undefined ? 0 : options.offset;
	const onScreen = !options ||  options.onScreen === undefined ? false : options.onScreen;
	const top = el.getBoundingClientRect().top;

	if(!onScreen || (top < offset || top > window.innerHeight)) {
		let pos = top + Core.scrollTop() - offset;

		window.scroll({top: pos, left: 0, behavior: "smooth"});
	}
};

/**
 * Get the distance in pixels to the top of the page
 */
Core.scrollTop = function() {
	return document.documentElement.scrollTop || document.body.scrollTop;
};

/**
 * Get the index of an element within its parent
 */
Core.index = function(el) {
	let index = 0;

	while(el = el.previousElementSibling) {
		index++;
    }

	return index;
};

/**
 * Triggers an event on an element
 */
Core.trigger = function(el, name) {
	const event = document.createEvent("HTMLEvents");
	event.initEvent(name, true, false);
	el.dispatchEvent(event);
};

/**
 * Animate an element using css transitions
 */
Core.animate = function(el, duration, props) {
	const old = el.style.transition;
	let transition = "";
	let count = 0;
	let counter = 0;
	const easing = props.ease || "linear";
	const delay = props.delay || 0;
	const complete = props.complete || null;

	for(let key in props) {
		if(key == "ease" || key == "delay" || key == "complete") {
			delete props[key];
			continue;
		}

		const str = key.replace(/([a-z])([A-Z])/g, "$1-$2");
		const tmp = str.toLowerCase() + " " + duration + "s " + easing + " " + delay + "s, ";

		transition += tmp;
		count++;
	}

	el.style.transition = transition.substring(0, transition.length - 2);

	for(let key in props) {
		const str = key.replace(/([a-z])([A-Z])/g, "$1-$2");

		el.style[key] = props[key];
	}

	el.addEventListener("transitionend", end);

	const t = setTimeout(end2, duration * 1000 + delay * 1000 + 50);

	function end(e) {
		if(++counter < count || e.target != e.currentTarget) {
			return false;
		}

		clearTimeout(t);
		counter = 0;
		end2();
	}

	function end2() {
		el.removeEventListener("transitionend", end);
		el.style.transition = old;

		if(complete) {
			complete.call();
		}
	}
};

/**
 * Show or hide an element with a slide animation
 */
Core.slideToggle = function(el, complete) {

	let height = el.offsetHeight;
	let padding = parseInt(getComputedStyle(el)["padding-top"], 10) + parseInt(getComputedStyle(el)["padding-bottom"], 10);

	// Slide up
	if(height - padding > 0) {
		el.style.height = height + "px";

		void el.offsetWidth;

		el.style.height = "0";
		el.addEventListener("transitionend", onEndHide);
	}
	// Slide down
	else {
		el.style.height = "auto";
		height = el.offsetHeight;
		el.style.height = "0";
		el.style.visibility = "visible";

		void el.offsetWidth;

		el.style.height = height + "px";
		el.addEventListener("transitionend", onEndShow);
	}

	function onEndHide() {
		el.style.visibility = "hidden";

		el.removeEventListener("transitionend", onEndHide);

		if(complete) {
			complete();
		}
	}

	function onEndShow() {
		if(el.offsetHeight - padding > 0) {
			el.style.height = "auto";
		}

		el.removeEventListener("transitionend", onEndShow);

		if(complete) {
			complete();
		}
	}
};

/**
 * Trap focus inside an element
 */
Core.trapFocus = function(el) {
	let focusableElements = [].slice.call(el.querySelectorAll("button, [href], input, select, textarea, [tabindex='0']"));
	focusableElements = focusableElements.filter(item => item.getAttribute("tabindex") != "-1");

	const firstFocusableElement = focusableElements[0];
	const lastFocusableElement = focusableElements[focusableElements.length - 1];

	el.addEventListener("keydown", function(e) {
		var key = e.which || e.keyCode;

		if(key != 9) {
			return;
		}

		if(e.shiftKey) {
			if(document.activeElement === firstFocusableElement) {
				lastFocusableElement.focus();
				e.preventDefault();
			}
		} else {
			if(document.activeElement === lastFocusableElement) {
				firstFocusableElement.focus();
				e.preventDefault();
			}
		}
	});
}

// Add global shortcuts $ and $$
if(!(typeof $ != "undefined")) {
	var $ = Core;
}

const $$ = Core.$$;

class Header {

	constructor(el) {
		this.el = el;

		this.build();
	}

	/*
	 * Build the header
	 */
	build() {
		const options = {
			rootMargin: "2px 0px 0px 0px"
		};

		const observer = new IntersectionObserver(entries => {
			entries.forEach(item => {
				if(item.isIntersecting) {
					this.el.classList.remove("small");
				} else {
					this.el.classList.add("small");
				}
			});
		}, options);

		observer.observe(this.el);
	}
}

class Navigation {

	constructor(el) {
		this.el = el;
		this.parentIsLink = true;
		this.toggleButton = document.querySelector("#nav-button");
		this.touched = false;
		this.navOpen = false;

		this.build();
	}

	/**
	 * Build the navigation
	 */
	build() {
		// Menu button
		this.toggleButton.addEventListener("click", e => {
			if(!this.isMobileNavOpen()) {
				this.open();
			}
			else {
				this.close();
			}
		});

		// Close
		document.body.addEventListener("click", e => {
			if(!this.el.contains(e.target) && this.isMobileNavOpen()) {
				this.close();
			}
		});

		// Esc key
		this.el.addEventListener("keydown", e => {
			let key = e.which || e.keyCode;

			if(key == 27) {
				// Close sub nav
				if(!this.isMobileNavOpen()) {
					this.el.querySelectorAll(".subnav-button").forEach(item => {
						item.classList.remove("active");
					});

					this.el.querySelectorAll("ul").forEach(item => {
						item.classList.remove("active");
					});
				}

				// Close mobile nav
				this.close();
			}
		});

		// Remove transition when animation complete
		this.el.addEventListener("transitionend", e => {
			if(e.target == this.el.querySelector("div") && e.propertyName == "transform") {
				document.documentElement.classList.remove("nav-open-transition");
			}
		});

		// Add events to parent items
		this.el.querySelectorAll(".menu-item-has-children").forEach(i => {
			i.querySelector("a").insertAdjacentHTML("afterend", '<button class="subnav-button" aria-label="Open sub-menu"><svg width="12" height="8" viewBox="0 0 12 8" xmlns="http://www.w3.org/2000/svg"><path d="M6.748 6.748a1.052 1.052 0 01-1.496 0L.31 1.808A1.059 1.059 0 011.722.24L1.8.31 6 4.505 10.193.31a1.059 1.059 0 011.567 1.411l-.07.078" fill="#000" fill-rule="nonzero"/></svg></button>');

			const subnavButton = i.querySelector(".subnav-button");

			// Subnav button
			subnavButton.addEventListener("click", e => {
				e.stopPropagation();

				// if(!i.querySelector("ul").classList.contains("active")) {
				// 	this.el.querySelectorAll(".menu-item-has-children ul").forEach(list => {
				// 		list.classList.remove("active");
				// 	});
				// }
				this.toggleChildren(i, e.currentTarget);

				if(!this.isMobileNavOpen()) {
					if(i.querySelector("ul").classList.contains("active")) {
						this.touched = true;
						this.checkSubNavPosition(i);
					}
					else {
						this.touched = false;
					}
				}
			});

			subnavButton.setAttribute("aria-expanded", false);

			// Touch support for hover menu
			i.addEventListener("touchstart", e => {
				e.stopPropagation();

				if(!i.closest(".hover")) {
					this.clearSubNav();
				}

				if(!this.isMobileNavOpen()) {
					if(!this.touched || !i.classList.contains("hover")) {
						e.preventDefault();
					}

					if(!i.classList.contains("hover")) {
						i.classList.add("hover");
						this.touched = true;
						this.checkSubNavPosition(i);
					}
				}
			});

			i.addEventListener("mouseenter", e => {
				if(!this.isMobileNavOpen()) {
					if(!this.touched) {
						this.checkSubNavPosition(i);
					}
				}
			});

			i.addEventListener("mouseleave", e => {
				const ul = i.querySelector("ul");

				if(!this.isMobileNavOpen()) {
					i.classList.remove("hover");
					ul.classList.remove("left");
					ul.classList.remove("right");
					ul.classList.remove("active");
				}
			});
		});

		// Close all when touched outside
		document.addEventListener("touchstart", e => {
			if(!this.el.contains(e.target)) {
				this.clearSubNav();
			}
		});

		// Show active menu on page load
		this.el.querySelectorAll(".current-page-ancestor > ul").forEach(item => {
			const button = item.parentNode.querySelector(".subnav-button");

			item.classList.toggle("active-mobile");
			button.classList.add("active");
			button.setAttribute("aria-expanded", button.classList.contains("active"));
		});
	}

	/**
	 * Check if mobile nav is open
	 */
	isMobileNavOpen() {
		return document.documentElement.classList.contains("nav-open") && this.toggleButton.offsetParent;
	}

	/**
	 * Hide the floating sub nav
	 */
	clearSubNav() {
		this.el.querySelectorAll(".menu-item-has-children").forEach(item => {
			item.classList.remove("hover");
		});

		this.touched = false;
	}

	/**
	 * Check if the sub nav is out of bounds
	 */
	checkSubNavPosition(el) {
		const ul = el.querySelector("ul");
		const pos = ul.getBoundingClientRect().left;
		const margin = 10;

		if(pos + ul.offsetWidth > window.innerWidth - margin) {
			ul.classList.add("right");
		}

		if(pos < margin) {
			ul.classList.add("left");
		}
	}

	/**
	 * Open the mobile nav
	 */
	open() {
		document.documentElement.classList.add("nav-open");
		document.documentElement.classList.add("nav-open-transition");

		this.el.querySelector("div").scrollTop = 0;
		this.toggleButton.setAttribute("aria-expanded", true);
		this.navOpen = true;
	}

	/**
	 * Close the mobile nav
	 */
	close() {
		document.documentElement.classList.remove("nav-open");
		document.documentElement.classList.add("nav-open-transition");

		this.toggleButton.setAttribute("aria-expanded", false);
		this.navOpen = false;
	}

	/**
	 * Toggle the sub nav
	 */
	toggleChildren(item, button) {
		// Toggle with animation
		if(this.isMobileNavOpen()) {
			Core.slideToggle(item.querySelector("ul"), () => {
				if(item.querySelector("ul").style.visibility == "hidden") {
					// item.classList.remove("hover");
					button.classList.remove("active");
				}
				else {
					// item.classList.add("hover");
					button.classList.add("active");
				}
			});
		}
		else {
			item.querySelector("ul").classList.toggle("active");
		}

		// Toggle without animation
		// item.querySelector("ul").classList.toggle("active");

		// item.classList.toggle("hover");
		button.classList.toggle("active");
		button.setAttribute("aria-expanded", button.classList.contains("active"));
	}
}

class App {

	constructor(queue) {
		this.isReady = false;
		this.readyQueue = queue;

		this.init();
	}

	/**
	 * Initialize app when DOM ready
	 */
	init() {
		Core.ready(() => {
			this.isReady = true;
			this.start();
			this.processQueue();
		});
	}

	/**
	 * Add function to the ready queue
	 */
	ready(fn) {
		if(this.isReady) {
			fn();
		} else {
			this.readyQueue.push(fn);
		}
	}

	/**
	 * Process the ready queue
	 */
	processQueue() {
		while(this.readyQueue.length) {
			this.readyQueue.shift()();
		}
	}

	/**
	 * App entry point
	 */
	start() {
		// Focus visible
		this.focusVisible();

		// Header
		new Header(document.querySelector("#header"));

		// Navigation
		new Navigation(document.querySelector("#navigation"));
	}

	/**
	 * Add focus outline to buttons only when keyboard-focused
	 */
	focusVisible() {
		document.documentElement.classList.add("focus-visible");

		const onBlur = e => {
			e.target.classList.remove("focus-visible");
		};

		document.body.addEventListener("keyup", e => {
			var key = e.which || e.keyCode;

			if(key === 9) {
				const activeElement = document.activeElement;

				if(!activeElement.classList.contains("focus-visible")) {
					activeElement.classList.add("focus-visible");
					activeElement.addEventListener("blur", onBlur);
				}
			}
		});
	}
}

app = new App(app.queue);
